$default-transition-speed: 0.3s;
$default-transition-easing: ease-out;

$default-transition: all $default-transition-speed $default-transition-easing;

a {
  &:not(.button) {
    &:active {
      color: #4C5E6E;
      outline: none !important; } } }

%default-transition {
  transition: $default-transition; }

[class*="js-"] {
  cursor: pointer; }

.s-animate {
  // all icons
  @extend %default-transition; }

.logo {
  display: block;
  @include bg-retina('logo', 218, 95);

  .is-scrolled & {
    @include bg-retina('logo', 132, 58); }

  @include media('<lg') {
    @include bg-retina('logo', 132, 58); }

  .theme--intro-light & {
    :not(.is-scrolled) & {
      @include media('>lg') {
        @include bg-retina('logo-white', 218, 95); } } } }

.logo-footer-truenth {
  @include bg-retina('TrueNTH-Footer-Logo', 79, 91); }

.logo-footer-movember {
  @include bg-retina('Movember-Footer-Logo', 86, 86); }

.button {
  @extend %default-transition;
  @include fz(16px, 1);
  text-transform: uppercase;
  padding: 15px;
  letter-spacing: 3px;

  &:focus,
  &:visited,
  &:active {
    outline: none !important;
    text-decoration: none !important; }

  &:active {
    color: #4C5E6E; }

  // sizes

  &--large {
    @include fz(22px, 1);
    letter-spacing: 5px;
    min-width: 218px;
    padding: 25px 30px; }

  &--small {
    @include fz(12px, 1);
    padding: 16px;
    max-height: 45px; }

  // colors

  &--teal {
    @include make-button($teal, white); }

  &--white {
    @include make-button(white, $teal, $gray-lighter); }

  &--facebook {
    @include make-button(#3A579A, white); }

  &--google {
    @include make-button(#E04B33, white); }

  &--white-trans {
    border: 2px solid #fff;
    color: #FFF;
    &:hover {
      background: transparent !important;
      color: $gray-lightest;
      border-color: $gray-lighter; } }

  &--facebook {
    svg {} }

  &--google {
    svg {
      top: 10px; } }

  &--has-svg {
    position: relative;
    display: inline-block;
    text-align: left;
    padding: 28px 0 0 70px !important;
    @include pseudo-el(1, 22, rgba(255,255,255,.2));
    margin: 0 0 10px;

    &::after {
      position: absolute;
      left: 55px;
      top: 50%;
      margin-top: -11px;

      @include media('<md') {
        display: none; } }

    svg {
      position: absolute;
      left: 20px;
      top: 20px; } }

  &--scrolled {
    display: none;
    .is-scrolled & {
      display: block; } }

  &--not-scrolled {
    display: block;
    .is-scrolled & {
      display: none; } } }

.button-wrap {
  &--centered {
    text-align: center; } }

%accent-heading-super {
  @include fz(9px, 1);
  font-style: normal;
  position: relative;
  top: 1px;
  vertical-align: super; }

.accent-heading {
  @include pseudo-el(233, 1, rgba(255, 255, 255, .5));
  color: $teal-light;
  text-align: center;
  text-transform: uppercase;
  font-weight: 500;
  @include fz(16px, 20px);
  padding-bottom: 25px;
  letter-spacing: 2.9px;
  display: inline-block;
  width: 100%;
  clear: both;

  &::after {
    position: relative;
    margin: 25px 0 0;
    left: 50%;
    margin-left: -233/2 + px; }

  &.accent-heading--no-border {
    padding-bottom: 40px;

    &::after {
      display: none; } }

  em {
    @extend %accent-heading-super; } }

.outer-container {
  @extend .container; }

.more-arrow {
  @extend .clearfix;
  @include fz(15px, 1);
  letter-spacing: 3px;
  text-transform: uppercase;
  text-align: center;
  padding: 30px 0;

  > a {
    @extend %default-transition;
    color: #FFF;

    &:hover {
      color: $gray; } }

  span {
    display: inline-block;
    position: relative;
    padding-right: 40px; }

  figure {
    @extend .s;
    @extend .s-arrow-right;
    color: #586C73;
    @include fz(31px, 15px);
    position: absolute;
    right: 0;
    top: -8px; }

  &--teal {
    color: $teal;

    &:hover {
      color: $teal-light; } } }

.photo-grid-container {
  @extend .outer-container; }

.photo-grid {
  // +make-row
  width: 100%;

  &__item {
    @include make-col;
    @include make-col-span(3);
    padding-left: 0;
    padding-right: 0;
    position: relative;
    color: #FFF;
    margin: 0;

    @include media('<md') {
      @include make-col-span(6); }

    &:hover {
      color: #FFF;

      .photo-grid__img {
        z-index: 0; }
      .photo-grid__img--hovered {
        display: block;
        width: 100%;
        height: 100%; }
      strong {
        opacity: 1; } }

    strong {
      transition: opacity $default-transition-speed $default-transition-easing;
      opacity: 0;
      position: absolute;
      bottom: 15px;
      padding: 0 15px;
      z-index: 10;
      span {
        display: block; } } }

  &__img {
    &--hovered {
      position: absolute;
      left: 0;
      top: 0;
      z-index: 0;
      display: none; } } }

.button-callout {
  padding: 40px 0;
  text-align: center;

  > strong {
    @include fz(16px, 1);
    color: $teal-light;
    display: block;
    text-transform: uppercase;
    letter-spacing: 2.9px;
    margin-top: 55px; }

  > a.button {
    display: inline-block; }

  figure {
    @extend .s;
    @extend .s-caret-down;
    @include fz(30px, 1);
    color: $teal-light;
    display: block;
    margin: 40px 0 0; }

  &.button-callout--has-text .button-callout__scroll {
    margin-top: 30px; } }

.next-down {
  text-align: center;
  padding-bottom: 30px;

  &__copy {
    @include fz(25px, 32px);
    letter-spacing: 0.3px;
    padding: 50px 0 30px;
    margin: 0;

    @include media('<md') {
      @include fz(24px, 33px); } }

  figure {
    @extend .s;
    @extend .s-caret-down;
    @include fz(30px, 1);
    color: $teal; } }

.mini-boxes {
  // +make-row
  display: flex;
  flex-wrap: wrap;

  &--full-width {
    margin: 0 auto;
    max-width: $content-max-width; }

  &__item {
    // +make-col
    @include make-col-span(4);
    padding: 0 $grid-gutter-width/2 $grid-gutter-width $grid-gutter-width/2;
    display: flex;

    &-col {
      flex-direction: column;
      .mini-box {
        width: 100%;
        flex: 1;
        margin-bottom: $grid-gutter-width;

        &:last-of-type {
          margin-bottom: 0; } } }

    .mini-boxes--half-width & {
      @include make-col-span(6);

      @include media('<md') {
        @include make-col-span(12); } }

    .mini-boxes--full-width & {
      @include make-col-span(12); }

    @include media('<md') {
      @include make-col-span(6); } } }

.mini-box {
  background: rgba(51, 54, 56, 0.8);
  color: #FFF;
  padding: 20px;

  .mini-boxes--theme-brown & {
    background: rgba(57, 56, 51, 0.8); }

  .mini-boxes--theme-gray & {
    background: rgba(59, 68, 73, 0.5); }

  &__head {
    @include fz(17px, 23px);
    color: $gold;
    letter-spacing: 1.2px;
    margin: 0 0 10px;
    text-transform: uppercase;

    &--centered {
      text-align: center; }

    @include media('<md') {
      @include fz(14px, 18px); } }

  &__subhead {
    @include fz(19px, 25px);
    font-weight: bold;
    margin: 15px 0 5px;

    .mini-boxes--theme-blue & {
      color: #BCC2A4; } }

  &__copy,
  &__list {
    margin: 0;
    @include fz(19px, 25px);

    &--smaller {
      @include fz(17px, 25px); }

    @include media('<md') {
      @include fz(15px, 20px); } }

  &__list {
    li {
      @include fz(22px, 30px); }

    &.mini-box__list--inline {
      margin-top: 15px;
      li {
        @include fz(19px, 25px); } } } }

.icon-box {
  @extend .clearfix;
  background: $gray-darker;
  color: #fff;
  display: block;
  padding: 20px 20px 20px 135px;
  margin: 0 10px 0 0;
  position: relative;
  min-height: 130px;
  max-height: 100%;

  &:active,
  &:focus,
  &:visited,
  &:hover {
    outline: none;
    color: #fff; }

  &:hover {
    color: #7C959E; }

  .icon-box-wrap & {
    padding-left: 130px;

    @include media('<sm') {
      padding: 130px 20px 20px 20px; } }

  strong, h4, span {
    display: block; }

  .icon-box-group & {
    width: 100%;
    margin-right: 0; }

  &--theme-brown {
    background: rgba(57, 56, 51, 0.8); }

  &.icon-box--theme-inactive {
    background: rgba(51, 54, 56, 0.5); }

  &.icon-box--theme-brown {
    background: rgba(57, 56, 51, 0.8);

    &.icon-box--theme-inactive {
      background: rgba(57, 56, 51, 0.5); } }

  &__subhead {
    @include fz(16px, 20px);
    font-weight: 500;
    letter-spacing: 2.9px;
    text-transform: uppercase;
    margin: 6px 0 10px;
    color: $teal-light;

    .icon-box-wrap & {
      @include fz(12px, 18px);

      em {
        font-size: 6px; } }

    .icon-box--theme-two & {
      color: #D2D1C5; }

    em {
      @extend %accent-heading-super;
      font-size: 10px; } }

  &__head {
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 1.14px;

    .icon-box--theme-inactive & {
      color: #BFC0BD; }

    .icon-box-wrap & {
      @include fz(22px, 28px); } }

  &__lowhead {
    color: #BCC2A6;
    @include fz(12px, 17px);
    text-transform: uppercase;
    letter-spacing: 2.35px;

    .icon-box--theme-two & {
      color: $teal-light; } }

  &__icon {
    position: absolute;
    left: 20px;
    top: 20px;

    @include media('<sm') {
      left: 50%;
      margin-left: -45px; } }


  &__button {
    @extend %default-transition;
    @include fz(22px, 26px);
    letter-spacing: 5px;
    color: #FFF;
    display: inline-block;
    padding: 25px;
    text-transform: uppercase;
    text-align: center;

    .icon-box & {
      width: 100%; }

    @include media('<md') {
      @include fz(20px, 24px);
      padding-right: 15px;
      padding-left: 15px; }

    span {
      position: relative;
      display: inline-block;
      padding-right: 45px;

      figure {
        @extend .s;
        @extend .s-arrow-right;
        @include fz(31px, 1);
        position: absolute;
        margin-left: 5px;
        color: $teal-dark;
        top: -5px;
        right: 0; } }

    &:hover {
      color: $gray-lighter; }

    &--main {
      background: $teal;
      margin: 0 0 10px;
      height: 72px;

      .page-intro & {
        min-width: 250px; }

      &:hover {
        background: darken($teal, 15%);

        figure {
          color: #fff; } } }

    &--more-info {
      background: $gold-alt;
      figure {
        color: darken($gold-alt, 15%); }

      &:hover {
        background: darken($gold-alt, 15%); }

      span {
        padding-right: 0; }
      @include media('>md') {
        @include fz(17px, 1);
        letter-spacing: 2px;
        padding: 20px;
        span {
          line-height: 32px; } } }

    &--sub {
      @include fz(14px, 18px);
      letter-spacing: 2px;
      padding: 20px; } } }

.icon-box-group__wrapper {
  @extend .outer-container; }

.icon-box-group {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  // +make-row

  @include media('<lg') {
    display: block; }

  .icon-box-wrap {
    float: left;
    width: 50%;
    padding: 0 24px 25px 0;

    @include media('<lg') {
      display: block;
      float: none;
      width: 100%;
      padding: 0 0 5px; }

    &:nth-child(2n) {
      padding-right: 0; }

    .icon-box-wrap__main {
      width: 100%;
      margin: 0; }

    // +make-col
 } }    // +make-col-span(6)

.icon-box-wrap {
  $small-size: 3.7;
  max-width: 715px;
  margin: 0 auto;

  @include media('>md') {
    display: flex; }

  @include media('<md') {
    display: block; }

  &__main {
    @include make-col-span(12);
    background: rgba(51, 54, 56, 0.8);
    @include media('>md') {
      display: flex; }

    @include media('<md') {
      padding-bottom: 100px; }

    &__left {
      @include make-col-span(7.5);

      @include media('<md') {
        @include make-col-span(12);
        margin-bottom: $grid-gutter-width; } }

    &__right {
      @include make-col-span(4.5);
      float: right;
      margin-top: 11px;

      @include media('<md', '>sm') {
        width: calc(100% - 30px);
        position: absolute;
        left: $grid-gutter-width; }

      @include media('<sm') {
        float: none;
        width: 100%; } }

    &.icon-box--theme-inactive {
      background: rgba(51, 54, 56, 0.5); }

    &.icon-box--theme-brown {
      background: rgba(57, 56, 51, 0.8);

      &.icon-box--theme-inactive {
        background: rgba(57, 56, 51, 0.5); } }

    @include media('<md') {
      @include make-col-span(12);
      margin-bottom: 10px; } }

  &__sub {
    @include make-col-span($small-size);

    > a {
      width: 100%; }

    @include media('<md') {
      @include make-col-span(12);
      display: flex;
      .icon-box__button {
        float: left;
        width: 100%;
        margin: 0;
        text-align: center;
        &--main {
          margin-right: 0; }
        &--sub {
          padding-top: 20px; } } } } }

a.profile-toggle {
  width: 45px;
  height: 45px;
  figure {
    @extend .s;
    @extend .s-caret-down;
    @include fz(25px, 1);
    left: -6px;
    top: -4px; } }

// bootstrap overrides
.dropdown-toggle::after {
  display: none; }

.header .dropdown-menu {
  left: auto;
  right: 0;
  border-radius: 0;
  margin: 0;
  padding: 0;
  box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.20);
  border: 0;

  a {
    @include fz(16px, 20px);
    margin-left: 0;
    padding: 15px 25px;
    border-bottom: 1px solid #fff;

    &:last-child {
      border-bottom: 0; }

    &:hover {
      background: #CCD0D3; } } }

.list-module {
  background: #BCC2A6;
  color: #586C73;
  padding: 50px 80px;

  @include media('<md') {
    padding: 50px 20px; }

  .accent-heading {
    color: #586C73 !important;
    &::after {
      background: #7C959E; } }

  &__items {
    @extend .list-unstyled; }

  &__item {
    @include fz(28px, 35px);
    margin: 20px 0; } }

.checkbox-item {
  padding-left: 80px;
  position: relative;

  @include media('<md') {
    padding-left: 50px; }

  &__check {
    width: 35px;
    height: 35px;
    background: #fff;
    position: absolute;
    left: 0;

    figure {
      @extend .s;
      @extend .s-check;
      position: absolute;
      left: 4px;
      top: 2px; } } }

// forms

select {
  color: $gray; }

.field-group {
  @extend .clearfix;
  @include make-row;
  > div {
    @include make-col;
    @include make-col-span(12); }
  &__half {
    > div {
      @include make-col-span(6); } } }

.field {
  label {
    @include fz(16px, 20px);
    color: #D2D1C4;
    letter-spacing: 3px;
    text-transform: uppercase;
    display: block;
    text-align: center;
    margin: 0 0 10px; }
  input,
  textarea,
  select {
    @extend %default-transition;
    @include fz(20px, 35px);
    background-color: #E3E0DB;
    border-radius: 4px;
    width: 100%;
    padding: 15px 20px;
    color: #4B5E6F;

    &:focus {
      background: lighten(#E3E0DB, 10%); } }

  textarea {
    min-height: 200px; }

  select {
    appearance: none;
    border-radius: 4px;
    height: 70px;
    padding-left: 15px;
    padding-right: 15px;
    background: #D2D1C4 url('/images/Arrow-Down-Light-Brown.svg') no-repeat scroll 95% 50%;

    &:focus {
      background: lighten(#E3E0DB, 10%) url('/images/Arrow-Down-Light-Brown.svg') no-repeat scroll 95% 50%; }

    option {
      padding-left: 15px;
      padding-right: 15px; } } }

.field-group {
  margin: 0 0 30px; }

.modal-backdrop {
  background: rgba(59,68,73,0.9);
  &.in {
    opacity: 1; } }

body.modal-open {
  .modal.is-modal-active {
    height: 100%;
    @include media('>md') {
      display: flex !important; }

    .modal-dialog {
      margin: auto;
      width: 450px;
      border-radius: 0;


      @include media('<md') {
        max-width: 320px;
        margin: 0 auto; }

      @include media('<sm') {
        width: 100%; } } } }

.modal-content {
  border-radius: 0;
  text-align: center; }

$box-modal-inner-width: 318px;

.box-modal {
  @extend .clearfix;
  color: #4B5E6F;
  position: relative;

  .button-large {
    display: inline-block;
    width: 100%;
    margin: 0 auto;
    overflow: hidden;
    height: 72px;
    @include fz(17px, 1);
    padding: 27px 15px;

    @include media('<md') {
      max-width: 100%;
      padding: 30px 0 0 55px !important;
      @include fz(12px, 1); } }


  &__divider {
    height: 2px;
    background: #E3E0DB;
    position: relative;
    margin: 10px auto 20px auto;
    width: 100%;

    @include media('>md') {
      width: $box-modal-inner-width; } }

  &__close {
    @extend .s;
    @extend .s-close-x;
    cursor: pointer;
    @include fz(30px, 1);
    position: absolute;
    right: 20px;
    top: 25px;
    color: #7C959E;
    z-index: 500; }

  &__inner {
    padding: 40px;

    @include media('<md') {
      padding: 20px; } }

  &__title {
    @include pseudo-el(128, 1, #4C5E6E);
    @include fz(28px, 33px);
    color: #4B5E6F;
    position: relative;
    padding: 0 0 20px;
    margin: 0 0 25px;
    font-weight: bold;
    &:after {
      opacity: .5;
      position: absolute;
      bottom: 0;
      left: 50%;
      margin-left: -64px; } }

  &__copy {
    @include fz(18px, 24px); }

  &__sub {
    @include fz(12px, 17px);
    color: #A9B2B8;
    text-transform: uppercase;
    display: block;
    letter-spacing: 3px;
    margin: 0 0 20px; }

  form {
    input {
      @extend %default-transition;
      display: inline-block;
      background: #E3E0DB;
      height: 44px;
      padding: 5px;
      border: 2px solid #E3E0DB;
      min-width: 215px;

      &:focus,
      &:active {
        border: 2px solid $teal;
        outline: none !important; } }

    button {
      display: inline-block;
      @extend .button;
      border: 0;
      @extend .button--teal;
      @extend .button--small;
      position: relative;
      top: -1px; } }

  &--login {
    form {
      input {
        display: block;
        margin: 0 auto 10px;
        width: 100%; } } }

  &__footer {
    @extend .clearfix;
    padding: 20px;
    background: rgba(181, 175, 167, 0.4);
    display: inline-block;
    clear: both;
    float: left;
    width: 100%;

    @include media('<md') {
      padding-left: 15px;
      padding-right: 15px; }

    form {
      input {
        background: #FFF;
        border-color: #FFF;

        @include media('<md') {
          min-width: 100px;
          width: 150px; } } }

    .box-modal__sub {
      color: $teal; } } }

// stubbed
form {
  .submitted-text {
    display: none; }
  &.is-submitted {
    input,
    button {
      display: none; }
    .submitted-text {
      display: block; } } }
